<template>
  <div class="application">
    <h1>Total Application Amount</h1>
    <form action="#" v-on:submit="checkApplicationAmount">
      <div class="columns is-mobile is-centered">
        <div class="column is-half">
          <div class="field is-grouped">
              <p class="control is-expanded">
                <input v-model.number="quantity" class="input" type="number" name="quantity" placeholder="Enter the quantity">
              </p>
              <p class="control">
                <button type="submit" class="button is-info">Submit</button>
              </p>
          </div>

          <p v-if="error" class="help is-danger">{{ error }}</p>
          <p v-if="totalPrice" class="help is-info">The total price for the application is {{ this.totalPrice | toCurrency }}</p>

        </div>
      </div>
    </form>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      quantity: null,
      totalPrice: null,
      error: null
    }
  },
  methods: {
    // Clear properties
    clear() {
      this.quantity = null;
      this.totalPrice = null;
      this.error = null;
    },

    // Make request
    checkApplicationAmount() {
      this.error = null;
      const config = {
        params: {
          quantity: this.quantity
        }
      }

      this.clear();

      // Call laravel endpoint
      this.$http.get('http://localhost/api/inventory', config).then(response => {
        this.totalPrice = response.body.applicationTotalPrice;
      }, response => {
        this.error = response.body.quantity[0];
      });
    }
  }
}
</script>

